import React, { useState } from 'react';


const Calculator = () => {

    const [isCBtnDown, setIsCBtnDown] = useState(false);
    const [isCEBtnDown, setIsCEBtnDown] = useState(false);
    const [is1BtnDown, setIs1BtnDown] = useState(false);
    const [is2BtnDown, setIs2BtnDown] = useState(false);
    const [is3BtnDown, setIs3BtnDown] = useState(false);
    const [is4BtnDown, setIs4BtnDown] = useState(false);
    const [is5BtnDown, setIs5BtnDown] = useState(false);
    const [is6BtnDown, setIs6BtnDown] = useState(false);
    const [is7BtnDown, setIs7BtnDown] = useState(false);
    const [is8BtnDown, setIs8BtnDown] = useState(false);
    const [is9BtnDown, setIs9BtnDown] = useState(false);
    const [is0BtnDown, setIs0BtnDown] = useState(false);
    const [isDecimalBtnDown, setIsDecimalBtnDown] = useState(false);
    const [isMultiplyBtnDown, setIsMultiplyBtnDown] = useState(false);
    const [isAdditionBtnDown, setIsAdditionBtnDown] = useState(false);
    const [isMinusBtnDown, setIsMinusBtnDown] = useState(false);
    const [isDivideBtnDown, setIsDivideBtnDown] = useState(false);
    const [isEqualBtnDown, setIsEqualBtnDown] = useState(false);

    const [result, setResult] = useState("0")

    const reset = () => {
        setResult("0");
    }

    const backSpace = () => {
        const r = result.slice(0, -1);
        try {
            if (r === "" || r === "ERRO") {
                setResult("0")
            } else {
                setResult(r)
            }
        } catch (error) {
            console.log(error)
        }
    }

    const Calculate = () => {
        try {
            // eslint-disable-next-line no-eval
            setResult(eval(result).toString())
        } catch (error) {
            setResult('ERROR')
        }
    }

    const onKeypadClick = button => {
        if (button === "=") {
            Calculate();
        } else if (button === "C") {
            reset();
        } else if (button === "CE") {
            backSpace();
        } else {
            result === "0" ? setResult(button) : setResult(result + button)
        }
    }

    return (
        <div className="calculator-wrapper">
            <div className="results-wrapper">
                <div className="results-container">{result} </div>
            </div>
            <div className="keypad-wrapper">
                <div className="keypad-row">
                    <div
                        className={isCBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsCBtnDown(true)}
                        onMouseUp={() => setIsCBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        C
                    </div>
                    <div
                        className={isCEBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsCEBtnDown(true)}
                        onMouseUp={() => setIsCEBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        CE
                    </div>
                </div>
                <div className="keypad-row">
                    <div
                        className={is1BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs1BtnDown(true)}
                        onMouseUp={() => setIs1BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        1
                    </div>
                    <div
                        className={is2BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs2BtnDown(true)}
                        onMouseUp={() => setIs2BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        2
                    </div>
                    <div
                        className={is3BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs3BtnDown(true)}
                        onMouseUp={() => setIs3BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        3
                    </div>
                    <div
                        className={isAdditionBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsAdditionBtnDown(true)}
                        onMouseUp={() => setIsAdditionBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        +
                    </div>
                </div>
                <div className="keypad-row">
                    <div
                        className={is4BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs4BtnDown(true)}
                        onMouseUp={() => setIs4BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}>
                        4
                    </div>
                    <div
                        className={is5BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs5BtnDown(true)}
                        onMouseUp={() => setIs5BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        5
                    </div>
                    <div
                        className={is6BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs6BtnDown(true)}
                        onMouseUp={() => setIs6BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        6
                    </div>
                    <div
                        className={isMinusBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsMinusBtnDown(true)}
                        onMouseUp={() => setIsMinusBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        -
                    </div>
                </div>
                <div className="keypad-row">
                    <div
                        className={is7BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs7BtnDown(true)}
                        onMouseUp={() => setIs7BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        7
                    </div>
                    <div
                        className={is8BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs8BtnDown(true)}
                        onMouseUp={() => setIs8BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        8
                    </div>
                    <div
                        className={is9BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs9BtnDown(true)}
                        onMouseUp={() => setIs9BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        9
                    </div>
                    <div
                        className={isMultiplyBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsMultiplyBtnDown(true)}
                        onMouseUp={() => setIsMultiplyBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        *
                    </div>
                </div>
                <div className="keypad-row">
                    <div
                        className={isDecimalBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsDecimalBtnDown(true)}
                        onMouseUp={() => setIsDecimalBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        .
                    </div>
                    <div
                        className={is0BtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIs0BtnDown(true)}
                        onMouseUp={() => setIs0BtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        0
                    </div>
                    <div
                        className={isEqualBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsEqualBtnDown(true)}
                        onMouseUp={() => setIsEqualBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        =
                    </div>
                    <div
                        className={isDivideBtnDown ? "keypad taskbar-invert" : "keypad"}
                        onMouseDown={() => setIsDivideBtnDown(true)}
                        onMouseUp={() => setIsDivideBtnDown(false)}
                        onClick={(e) => onKeypadClick(e.target.innerText)}
                    >
                        /
                    </div>
                </div>
            </div>

        </div>
    )
}

export default Calculator;