import React from 'react';
import { useGlobalContext } from '../../../context';

const StartMenuItem = ({ name, iconImg, id, setIsMenuOpen }) => {



    const { showAboutWindow, showCalculatorWindow, showDocumentsWindow, showSettingsWindow, showNotepadWindow, showBrowserWindow } = useGlobalContext();

    const onStartMenuItemClick = (id) => {
        const iconId = id;
        switch (iconId) {
            case 'about':
                showAboutWindow();
                setIsMenuOpen(false)
                break;
            case 'documents':
                showDocumentsWindow();
                setIsMenuOpen(false)
                break;
            case 'settings':
                showSettingsWindow();
                setIsMenuOpen(false);
                break;
            case 'notepad':
                showNotepadWindow();
                setIsMenuOpen(false);
                break;
            case 'calculator':
                showCalculatorWindow();
                setIsMenuOpen(false);
                break;
            case 'browser':
                showBrowserWindow();
                setIsMenuOpen(false);
                break;
            default:
                return null;
        }
    }

    return (
        <div className="startMenu-item" onClick={() => onStartMenuItemClick(id)}>
            <img src={iconImg} alt={name} />
            <div className="item-name">
                {name}
            </div>
        </div>
    )
}

export default StartMenuItem;