import React, { useRef } from 'react';
import '../../windows/window.scss'
import './calculator.scss';
import Draggable from 'react-draggable';
import Calculator from './calculator'
import closeIcon from '../../../../assets/icons/close-icon.gif';
import minIcon from '../../../../assets/icons/mini-icon.gif';
import maxIcon from '../../../../assets/icons/max-icon.gif';

import { useGlobalContext } from '../../../context';

const CalculatorWrapper = ({ desktopIcons }) => {

    const aboutRef = useRef()
    const { name, iconImg } = desktopIcons;
    const { closeCalculatorWindow, setIconFocused, setShowCalculatorWindow } = useGlobalContext();


    return (
        <Draggable
            handle=".handle"
            bounds="parent">
            <div className="window calculator-window" id="calculator" ref={aboutRef} onClick={() => setIconFocused('calculator')}>

                <div className="handle">
                    <div className="window-info">
                        <img src={iconImg} alt={name} />
                        {name}
                    </div>
                    <div className="window-controls">
                        <button className="control" onClick={() => setShowCalculatorWindow(false)}>
                            <img src={minIcon} alt="." />
                        </button>
                        <button className="control" >
                            <img src={maxIcon} alt="." />
                        </button>
                        <button className="control" onClick={() => closeCalculatorWindow()}>
                            <img src={closeIcon} alt="." />
                        </button>
                    </div>
                </div>
                <div className="window-container">
                    <div className="window-inner-container">
                        <Calculator />
                    </div>
                </div>


            </div>
        </Draggable >




    )

}

export default CalculatorWrapper;