import React, { useState } from 'react';
import '../../windows/window.scss'
import './browser.scss';
import Draggable from 'react-draggable';
import BrowserWindow from './browser-window';
import { ResizableBox } from 'react-resizable';

import stopIcon from '../../../../assets/icons/msg_error.png'
import homeIcon from '../../../../assets/icons/homepage.png';
import searchIcon from '../../../../assets/icons/search_web.png';
import favouritesIcon from '../../../../assets/icons/directory_favorites.png';
import historyIcon from '../../../../assets/icons/history.png';
import mailIcon from '../../../../assets/icons/mailbox_world.png';
import printIcon from '../../../../assets/icons/printer.png';
import closeIcon from '../../../../assets/icons/close-icon.gif';
import minIcon from '../../../../assets/icons/mini-icon.gif';
import maxIcon from '../../../../assets/icons/max-icon.gif';

import { useGlobalContext } from '../../../context';

const Browser = ({ desktopIcons }) => {


    const { name, iconImg } = desktopIcons;
    const { closeBrowserWindow, setIconFocused, setShowBrowserWindow } = useGlobalContext();
    const [browserWindowSize, setBrowserWindowSize] = useState({ w: window.innerWidth / 1.5, h: window.innerHeight / 1.2 })

    // //keep track of the size of the window
    // const onResize = (event, { element, size, handle }) => {
    //     setBrowserWindowSize({ w: size.width, h: size.height })
    // }



    return (
        <Draggable
            handle=".handle"
            bounds="parent"
        >
            <div className="window browser-window" id="browser" onClick={() => setIconFocused('browser')}>
                <ResizableBox
                    // onResize={onResize}
                    resizeHandles={['se']}
                    width={browserWindowSize.w} height={browserWindowSize.h}
                    minConstraints={[window.innerWidth / 3, 400]} maxConstraints={[window.innerWidth / 1.5, window.innerHeight / 1.2]}
                >
                    <div className="handle">
                        <div className="window-info">
                            <img src={iconImg} alt={name} />
                            {name}
                        </div>
                        <div className="window-controls">
                            <button className="control" onClick={() => setShowBrowserWindow(false)}>
                                <img src={minIcon} alt="." />
                            </button>
                            <button className="control" >
                                <img src={maxIcon} alt="." />
                            </button>
                            <button className="control" onClick={() => closeBrowserWindow()}>
                                <img src={closeIcon} alt="." />
                            </button>
                        </div>
                    </div>
                    <div className="window-container">
                        <div className="window-inner-container">
                            <div className="border-line"></div>
                            <div className="browser-top-wrapper">

                                <div className="browser-icons-wrapper">
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={stopIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            Stop
                                        </div>
                                    </div>
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={homeIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            Home
                                        </div>
                                    </div>
                                </div>
                                <div className="browser-icons-wrapper">
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={searchIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            Search
                                        </div>
                                    </div>
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={favouritesIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            Favourites
                                        </div>
                                    </div>
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={historyIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            History
                                        </div>
                                    </div>
                                </div>
                                <div className="browser-icons-wrapper">
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={mailIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            Mail
                                        </div>
                                    </div>
                                    <div className="browser-icon">
                                        <div className="browser-img">
                                            <img src={printIcon} alt="stop" />
                                        </div>
                                        <div className="browser-name">
                                            Print
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="border-line"></div>
                            <div className="address-bar-wrapper">
                                <div>Address:</div>
                                <div className="url-container">
                                    https://www.reddit.com/r/catgifs
                                </div>
                            </div>
                            <BrowserWindow />
                        </div>
                    </div>

                </ResizableBox>
            </div>
        </Draggable >




    )

}

export default Browser;