import React from 'react';
import './desktop-icon.scss';
import { useGlobalContext } from '../../context';

const DesktopIcon = ({ icon, iconName, iconId }) => {

    const { showAboutWindow, showDocumentsWindow, isBackgroundImageDefault, showSettingsWindow, showNotepadWindow, showCalculatorWindow, showBrowserWindow } = useGlobalContext();

    const onIconClick = (id) => {
        const iconId = id;
        switch (iconId) {
            case 'about':
                showAboutWindow();
                break;
            case 'documents':
                showDocumentsWindow();
                break;
            case 'settings':
                showSettingsWindow();
                break;
            case 'notepad':
                showNotepadWindow();
                break;
            case 'calculator':
                showCalculatorWindow();
                break;
            case 'browser':
                showBrowserWindow();
                break;
            default:
                return null;
        }
    }

    return (
        <div className="icon-wrapper" onClick={() => onIconClick(iconId)}>
            <img src={icon} alt={iconName} />
            <div className={isBackgroundImageDefault ? "icon-name " : "icon-name background-visibility"}>{iconName}</div>
        </div>
    )
}

export default DesktopIcon;