import React, { useEffect, useState } from 'react';
import './notifications.scss'
const Notifications = () => {

    const [time, setTime] = useState();

    function checkTime(i) {
        if (i < 10) {
            i = "0" + i;
        }
        return i;
    }

    useEffect(() => {
        setTimeout(function () {
            var today = new Date();
            var h = today.getHours();
            var m = today.getMinutes();
            // var s = today.getSeconds();
            var time = ""
            m = checkTime(m);
            // s = checkTime(s);
            if (h >= 12) {
                time = "PM"
            } else {
                time = "AM"
            }
            setTime(h + ":" + m + '' + time)
        }, 1000);
    })


    return (
        <div className="taskbar-bar taskbar-invert notification-wrapper">
            <div className="time">
                {time}
            </div>
        </div>
    )
}

export default Notifications;