import { map } from 'jquery';
import React, { useEffect, useState } from 'react';
import { useFetch } from '../../../../context';
import loadingIcon from '../../../../../assets/icons/loading.gif'

const BrowserWindow = () => {

    const res = useFetch("https://www.reddit.com/r/CatGifs.json", {})
    const [isDataLoaded, setIsDataLoaded] = useState(false)
    const [redditData, setRedditData] = useState({});



    useEffect(() => {
        if (res.response) {
            // console.log(res.response);
            const data = [];
            res.response.data.children.map((post, index) => {
                if (post.data.domain === 'i.redd.it' || post.data.domain === 'gfycat.com') {
                    data.push(post.data);
                }
            })
            console.log(data);

            setIsDataLoaded(true);

            setRedditData(data)
        }

    }, [res.response])


    if (!isDataLoaded) {
        return <div className="loading-screen">
            <img src={loadingIcon} alt="loading" />
            <p>loading...</p>
        </div>
    }

    // console.log(redditData)
    return (

        <div className="browser-window-container">
            {redditData.map((post, index) => {
                const author = `Posted by u/${post.author}`;
                const title = post.title;
                let media = "";
                const comments = post.num_comments;
                const upvotes = post.ups;
                const downvotes = post.downs;

                if (post.domain === "i.redd.it") {
                    media = post.url
                } else {
                    media = post.media.oembed.thumbnail_url
                }
                // if (post.data.domain === "i.redd.it") {
                //     media = post.data.url;
                // } else if (post.data.domain === "gfycat.com") {
                //     media = post.data.media.oembed.thumbnail_url;
                // }

                return (
                    <div key={index} className="reddit-post-container">
                        <div className="author">{author}</div>
                        <div className="title">{title}</div>
                        <div className="media">
                            <img src={media} alt={title} />
                        </div>
                        <div className="post-info">
                            <div className="comments">comments: {comments}</div>
                            <div className="upvotes"> upvotes: {upvotes}</div>
                            <div className="downvotes">downvotes: {downvotes}</div>
                        </div>
                    </div>
                )
            })}
        </div>
    )
}

export default BrowserWindow;