import React from 'react';
import { useGlobalContext } from '../../../context';

const AppTaskBar = ({ name, id, iconImg }) => {

    const { iconFocused, setIconFocused, setShowAboutWindow, setShowDocumentsWindow, setShowSettingsWindow, setShowNotepadWindow, setShowCalculatorWindow, setShowBrowserWindow } = useGlobalContext();
    return (
        <div
            id={id}
            className={iconFocused === id ? "apptaskbar-wrapper taskbar-bar taskbarApp-invert" : "apptaskbar-wrapper taskbar-bar"}
            onClick={() => {
                setIconFocused(id)
                if (id === "about") {
                    setShowAboutWindow(true);
                } else if (id === "documents") {
                    setShowDocumentsWindow(true);
                } else if (id === "settings") {
                    setShowSettingsWindow(true)
                } else if (id === "notepad") {
                    setShowNotepadWindow(true);
                } else if (id === "calculator") {
                    setShowCalculatorWindow(true);
                } else if (id === "browser") {
                    setShowBrowserWindow(true);
                }
            }}>
            <img src={iconImg} alt={name} />
            <div>
                {name}
            </div>
        </div>
    )
}

export default AppTaskBar;