import React, { useRef, useState } from 'react';
import '../../windows/window.scss'
import './settings.scss';
import BackgroundChanger from './background-changer';
import Draggable from 'react-draggable';
import { ResizableBox } from 'react-resizable';
import closeIcon from '../../../../assets/icons/close-icon.gif';
import minIcon from '../../../../assets/icons/mini-icon.gif';

import { useGlobalContext } from '../../../context';

const About = ({ desktopIcons }) => {

    const aboutRef = useRef()
    const { name, iconImg } = desktopIcons;
    const { isSettingsWindowShown, closeSettingsWindow, setIconFocused, setShowSettingsWindow } = useGlobalContext();
    const [settingsWindowSize, setSettingsWindowSize] = useState({ w: 400, h: 'auto' })
    // const [aboutWindowPosition, setAboutWindowPosition] = useState({ top: window.innerHeight / 4, left: window.innerWidth / 4 })

    //keep track of the size of the window
    const onResize = (event, { element, size, handle }) => {
        setSettingsWindowSize({ w: size.width, h: size.height })
    }

    if (isSettingsWindowShown) {
        return (
            <Draggable
                handle=".handle"
                bounds="parent">
                <div className="window settings-window" id="settings" ref={aboutRef} onClick={() => setIconFocused('settings')}>
                    <ResizableBox
                        onResize={onResize}
                        resizeHandles={[]}
                        width={settingsWindowSize.w} height={settingsWindowSize.h}
                        minConstraints={[380, 250]} maxConstraints={[window.innerWidth / 2.5, window.innerHeight / 1.5]}
                    >
                        <div className="handle">
                            <div className="window-info">
                                <img src={iconImg} alt={name} />
                                {name}
                            </div>
                            <div className="window-controls">
                                <button className="control" onClick={() => setShowSettingsWindow(false)}>
                                    <img src={minIcon} alt="." />
                                </button>
                                <button className="control" onClick={() => closeSettingsWindow()}>
                                    <img src={closeIcon} alt="." />
                                </button>
                            </div>
                        </div>
                        <div className="window-container">
                            <div className="window-inner-container">
                                <BackgroundChanger />
                            </div>
                        </div>

                    </ResizableBox>
                </div>
            </Draggable >




        )
    }

    return null;
}

export default About;