import React, { useContext, useState } from 'react';
import defaultWallpaper from '../../assets/default-wallpaper.png'


const AppContext = React.createContext();

const AppProvider = ({ children }) => {

    const [isLoading, setIsLoading] = useState(true);
    const [isAboutWindowShown, setShowAboutWindow] = useState(false);
    const [isDocumentsWindowShown, setShowDocumentsWindow] = useState(false);
    const [isSettingsWindowShown, setShowSettingsWindow] = useState(false);
    const [isNotepadWindowShown, setShowNotepadWindow] = useState(false);
    const [isCalculatorWindowShown, setShowCalculatorWindow] = useState(false);
    const [isBrowserWindowShown, setShowBrowserWindow] = useState(false);
    const [iconFocused, setIconFocused] = useState(); //tracks the focused application so it can be displayed on top
    const [openApplications, setOpenApplications] = useState(new Set()); //tracks the list of open applications - set is used instead of array to avoid duplicates.
    const [backgroundImage, setBackgroundImage] = useState(defaultWallpaper);
    const [backgroundPreview, setBackgroundPreview] = useState('(None)')
    const [isBackgroundImageDefault, setIsBackgroundImageDefault] = useState(true)


    const changeBackgroundImage = (img) => {
        setBackgroundImage(img)
    }

    const closeBrowserWindow = () => {
        openApplications.delete("browser");
        setOpenApplications(openApplications);
        setShowBrowserWindow(false);
        setIconFocused('');
    }

    const showBrowserWindow = () => {
        setShowBrowserWindow(true);
        setOpenApplications(openApplications.add("browser"));
        setIconFocused('browser')
    }

    const closeCalculatorWindow = () => {
        openApplications.delete('calculator');
        setOpenApplications(openApplications);
        setShowCalculatorWindow(false);
        setIconFocused('');
    }

    const showCalculatorWindow = () => {
        setShowCalculatorWindow(true);
        setOpenApplications(openApplications.add('calculator'));
        setIconFocused('calculator')
    }

    const closeNotepadWindow = () => {
        openApplications.delete('notepad');
        setOpenApplications(openApplications);
        setShowNotepadWindow(false);
        setIconFocused('');
    }

    const showNotepadWindow = () => {
        setShowNotepadWindow(true);
        setOpenApplications(openApplications.add('notepad'));
        setIconFocused('notepad')
    }

    const closeSettingsWindow = () => {
        openApplications.delete('settings');
        setOpenApplications(openApplications)
        setShowSettingsWindow(false);
        setIconFocused('')
    }

    const showSettingsWindow = () => {
        setShowSettingsWindow(true);
        setOpenApplications(openApplications.add('settings'));
        setIconFocused('settings');
    }

    const closeAboutWindow = () => {
        openApplications.delete('about')
        setOpenApplications(openApplications);
        setShowAboutWindow(false);
        setIconFocused('')
    }
    const showAboutWindow = () => {
        setShowAboutWindow(true);
        setOpenApplications(openApplications.add("about"));
        setIconFocused('about');
    }
    const closeDocumentsWindow = () => {
        setShowDocumentsWindow(false);
        openApplications.delete('documents')
        setOpenApplications(openApplications);

    }
    const showDocumentsWindow = () => {
        setShowDocumentsWindow(true);
        setOpenApplications(openApplications.add("documents"));
        setIconFocused('documents');
    }

    return (
        <AppContext.Provider
            value={{ isLoading, setIsLoading, showAboutWindow, setShowAboutWindow, closeAboutWindow, isAboutWindowShown, closeDocumentsWindow, showDocumentsWindow, isDocumentsWindowShown, iconFocused, setIconFocused, openApplications, setOpenApplications, setShowDocumentsWindow, backgroundImage, setBackgroundImage, isBackgroundImageDefault, setIsBackgroundImageDefault, closeSettingsWindow, showSettingsWindow, isSettingsWindowShown, setShowSettingsWindow, changeBackgroundImage, backgroundPreview, setBackgroundPreview, isNotepadWindowShown, setShowNotepadWindow, closeNotepadWindow, showNotepadWindow, closeCalculatorWindow, showCalculatorWindow, isCalculatorWindowShown, setShowCalculatorWindow, showBrowserWindow, closeBrowserWindow, isBrowserWindowShown, setShowBrowserWindow }}
        >
            {children}
        </AppContext.Provider>
    );
};

export const useGlobalContext = () => {
    return useContext(AppContext);
};

export const useFetch = (url, options) => {
    const [response, setResponse] = React.useState(null);
    const [error, setError] = React.useState(null);
    React.useEffect(() => {
        const fetchData = async () => {
            try {
                const res = await fetch(url, options);
                const json = await res.json();
                setResponse(json);
            } catch (error) {
                setError(error);
            }
        };
        fetchData();
    }, []);
    return { response, error };
};

export { AppContext, AppProvider };