import React from 'react';
import Start from './start';
import Notifications from './notifications';
import AppTaskBar from './applications';
import { useGlobalContext } from '../../context';

import './taskbar.scss'
const Taskbar = ({ desktopIcons, props }) => {

    const { openApplications } = useGlobalContext();
    return (
        <div className="taskbar-wrapper">
            <Start props={props} />

            {
                Array.from(openApplications).map((app) => {
                    const currentApp = desktopIcons[app];
                    return <AppTaskBar name={currentApp.name} key={currentApp.id} id={currentApp.id} iconImg={currentApp.iconImg} />
                })
            }

            <Notifications />
        </div>
    )
}

export default Taskbar;