import React from 'react';
import StartMenuItem from './startMenuItem';
import shutdownIcon from '../../../assets/icons/shutdown-icon.png'
import './startMenu.scss';


const StartMenu = ({ desktopIcons, setIsMenuOpen }) => {



    return (
        <div className="startMenu-wrapper">
            {
                Object.keys(desktopIcons).map((icon) => {
                    return <StartMenuItem
                        setIsMenuOpen={setIsMenuOpen}
                        id={desktopIcons[icon].id}
                        name={desktopIcons[icon].name}
                        iconImg={desktopIcons[icon].iconImg}
                        key={desktopIcons[icon].id}
                    />
                })
            }
            <div className="startMenu-line"></div>
            <div className="startMenu-item">
                <img src={shutdownIcon} alt="shutdown" />
                <div className="item-name">
                    <a href=".">Shut down...</a>
                </div>
            </div>
        </div>
    )

}

export default StartMenu;




