
import React, { useState, useEffect } from 'react';
import DesktopIcon from './dekstop-icon';
import About from './windows/about'
import Documents from './windows/documents'
import Settings from './windows/settings'
import Notepad from './windows/notepad';
import Calculator from './windows/calculator';
import Browser from './windows/browser';
import StartMenu from './startMenu';
import './desktop.scss';
//icons
import aboutIcon from '../../assets/icons/about-icon.png';
import documentsIcon from '../../assets/icons/documents-icon.png';
import settingsIcon from '../../assets/icons/settings-icon.png';
import notepadIcon from '../../assets/icons/notepad-icon.png';
import calculatorIcon from '../../assets/icons/calculator-icon.png';
import browserIcon from '../../assets/icons/browser.png'

import Taskbar from './taskbar';
import { useGlobalContext } from '../context';


const Desktop = () => {

    const [isMenuOpen, setIsMenuOpen] = useState(false);
    const { iconFocused, backgroundImage, isNotepadWindowShown, isSettingsWindowShown, isDocumentsWindowShown, isCalculatorWindowShown, isAboutWindowShown, isBrowserWindowShown } = useGlobalContext();

    const desktopIcons =
    {
        "about": { name: "About", id: 'about', iconImg: aboutIcon },
        "documents": { name: "Documents", id: 'documents', iconImg: documentsIcon },
        "notepad": { name: "Notepad", id: 'notepad', iconImg: notepadIcon },
        "browser": { name: "Internet Explorer", id: 'browser', iconImg: browserIcon },
        "settings": { name: "Settings", id: 'settings', iconImg: settingsIcon },
        "calculator": { name: "Calculator", id: 'calculator', iconImg: calculatorIcon },
    }

    //Close the menu when the user clicks anywhere on the desktop;
    useEffect(() => {
        const desktopEl = document.getElementsByClassName('desktop-wrapper')[0];
        desktopEl.addEventListener('click', (e) => {
            setIsMenuOpen(false)
        })
        return () => desktopEl.removeEventListener('click', setIsMenuOpen())
    }, [])

    //move the focused window to the top
    useEffect(() => {
        const parentEl = document.getElementsByClassName('desktop-wrapper')[0];
        const focusedEl = document.getElementsByClassName(`${iconFocused}-window`)[0]
        if (focusedEl !== undefined) {
            parentEl.append(focusedEl)
        }
    }, [iconFocused])


    return (
        <>
            <div className="desktop-wrapper" style={{ backgroundImage: `url(${backgroundImage})` }}>
                {isAboutWindowShown ? <About desktopIcons={desktopIcons["about"]} /> : null}
                {isDocumentsWindowShown ? <Documents desktopIcons={desktopIcons["documents"]} /> : null}
                {isSettingsWindowShown ? <Settings desktopIcons={desktopIcons["settings"]} /> : null}
                {isNotepadWindowShown ? <Notepad desktopIcons={desktopIcons["notepad"]} /> : null}
                {isCalculatorWindowShown ? <Calculator desktopIcons={desktopIcons["calculator"]} /> : null}
                {isBrowserWindowShown ? <Browser desktopIcons={desktopIcons["browser"]} /> : null}
                {
                    Object.keys(desktopIcons).map((icon) => {
                        return <DesktopIcon
                            key={desktopIcons[icon].id}
                            icon={desktopIcons[icon].iconImg}
                            iconName={desktopIcons[icon].name}
                            iconId={desktopIcons[icon].id}
                        />
                    })
                }
            </div>

            {isMenuOpen ? <StartMenu desktopIcons={desktopIcons} setIsMenuOpen={setIsMenuOpen} /> : null}
            <Taskbar props={{ isMenuOpen, setIsMenuOpen }} desktopIcons={desktopIcons} />
        </>
    )
}

export default Desktop;