import React, { useEffect, useState } from 'react';
import './backgroundChanger.scss'
import { useGlobalContext } from '../../../../context';
// eslint-disable-next-line no-unused-vars
import defaultWallpaper from '../../../../../assets/default-wallpaper.png'
// eslint-disable-next-line no-unused-vars
import bubblesWallpaper from '../../../../../assets/bubbles-wallpaper.png'
// eslint-disable-next-line no-unused-vars
import blueRivetWallpaper from '../../../../../assets/blue-rivets-wallpaper.png'
// eslint-disable-next-line no-unused-vars
import cravedStoneWallpaper from '../../../../../assets/craved-stone-wallpaper.png'
// eslint-disable-next-line no-unused-vars
import metalLinksWallpaper from '../../../../../assets/metal-links-wallpaper.png'
// eslint-disable-next-line no-unused-vars
import strawMatWallpaper from '../../../../../assets/straw-mat-wallpaper.png'
// eslint-disable-next-line no-unused-vars
import redBlockWallpaper from '../../../../../assets/red-block-wallpaper.png'

// previews
import strawMatWallpaperPreview from '../../../../../assets/straw_mat_preview.png'
import redBlockWallpaperPreview from '../../../../../assets/red_blocks_preview.png'
import defaultWallpaperReview from '../../../../../assets/default_preview.png'
import cravedStoneWallpaperPreview from '../../../../../assets/carved_stone_preview.png'
import bubblesWallpaperPreview from '../../../../../assets/bubbles_preview.png'
import blueRivetWallpaperPreview from '../../../../../assets/blue_rivets_preview.png'
import metalLinksPreview from '../../../../../assets/metal_links_preview.png'


const BackgroundChanger = () => {

    const { setBackgroundImage, setIsBackgroundImageDefault, backgroundPreview, setBackgroundPreview } = useGlobalContext();
    const [preview, setPreview] = useState(backgroundPreview);
    const [isMouseDown, setIsMouseDown] = useState(false);

    const changeBackground = ()=>{
        switch (backgroundPreview) {
            case "(None)":
                setIsBackgroundImageDefault(true);
                setBackgroundImage(defaultWallpaper)
                // setPreview(defaultWallpaperReview)
                break;
            case "Blue Rivets":
                setIsBackgroundImageDefault(false);
                setBackgroundImage(blueRivetWallpaper)
                // setPreview(blueRivetWallpaperPreview)
                break;
            case "Bubbles":
                setIsBackgroundImageDefault(false);
                setBackgroundImage(bubblesWallpaper)
                // setPreview(bubblesWallpaperPreview)
                break;
            case "Carved Stone":
                setIsBackgroundImageDefault(false);
                setBackgroundImage(cravedStoneWallpaper)
                // setPreview(cravedStoneWallpaperPreview)
                break;
            case "Metal Links":
                setIsBackgroundImageDefault(false);
                setBackgroundImage(metalLinksWallpaper)
                // setPreview(metalLinksPreview)
                break;
            case "Red Blocks":
                setIsBackgroundImageDefault(false);
                setBackgroundImage(redBlockWallpaper)
                // setPreview(redBlockWallpaperPreview)
                break;
            case "Straw Mat":
                setIsBackgroundImageDefault(false);
                setBackgroundImage(strawMatWallpaper)
                // setPreview(strawMatWallpaperPreview)
                break;
            default:
                return
    }
}

    useEffect(() => {
        switch (backgroundPreview) {
            case "(None)":
                // setIsBackgroundImageDefault(true);
                // setBackgroundImage(defaultWallpaper)
                setPreview(defaultWallpaperReview)
                break;
            case "Blue Rivets":
                // setIsBackgroundImageDefault(false);
                // setBackgroundImage(blueRivetWallpaper)
                setPreview(blueRivetWallpaperPreview)
                break;
            case "Bubbles":
                // setIsBackgroundImageDefault(false);
                // setBackgroundImage(bubblesWallpaper)
                setPreview(bubblesWallpaperPreview)
                break;
            case "Carved Stone":
                // setIsBackgroundImageDefault(false);
                // setBackgroundImage(cravedStoneWallpaper)
                setPreview(cravedStoneWallpaperPreview)
                break;
            case "Metal Links":
                // setIsBackgroundImageDefault(false);
                // setBackgroundImage(metalLinksWallpaper)
                setPreview(metalLinksPreview)
                break;
            case "Red Blocks":
                // setIsBackgroundImageDefault(false);
                // setBackgroundImage(redBlockWallpaper)
                setPreview(redBlockWallpaperPreview)
                break;
            case "Straw Mat":
                // setIsBackgroundImageDefault(false);
                // setBackgroundImage(strawMatWallpaper)
                setPreview(strawMatWallpaperPreview)
                break;
            default:
                return
        }
    }, [backgroundPreview])

    const onPreviewClick = (e) => {
        setBackgroundPreview(e.currentTarget.innerText);
    }
    return (
        <div className="background-changer-wrapper">
            <div className="background-monitor">
                <img src={preview} alt="" />
            </div>
            <div className="background-list-wrapper">
                <div className="list-title">
                    Wallpaper
                </div>
                <div className="list-container">
                    <ul>
                        <li
                            className={backgroundPreview === "(None)" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            (None)
                        </li>
                        <li
                            className={backgroundPreview === "Blue Rivets" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            Blue Rivets
                        </li>
                        <li
                            className={backgroundPreview === "Bubbles" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            Bubbles
                        </li>
                        <li
                            className={backgroundPreview === "Carved Stone" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            Carved Stone
                        </li>
                        <li
                            className={backgroundPreview === "Metal Links" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            Metal Links
                        </li>
                        <li
                            className={backgroundPreview === "Red Blocks" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            Red Blocks
                        </li>
                        <li
                            className={backgroundPreview === "Straw Mat" ? "active" : ""}
                            onClick={(e) => onPreviewClick(e)}>
                            Straw Mat
                        </li>
                    </ul>
                </div>

            </div>
            <div className="settings-cta-wrapper">
                <button 
                    className={isMouseDown ? "win-btn taskbar-invert" : "win-btn"}
                    onMouseDown={()=> setIsMouseDown(true)}
                    onMouseUp={()=>setIsMouseDown(false)}
                    onClick={()=> changeBackground()}
                    >
                    Apply
                </button>
            </div>
        </div>
    )
}

export default BackgroundChanger;