import React, { useRef, useState } from 'react';
import '../../windows/window.scss'
import './notepad.scss';
import Draggable from 'react-draggable';
import { ResizableBox } from 'react-resizable';
import closeIcon from '../../../../assets/icons/close-icon.gif';
import minIcon from '../../../../assets/icons/mini-icon.gif';
import maxIcon from '../../../../assets/icons/max-icon.gif';

import { useGlobalContext } from '../../../context';

const Notepad = ({ desktopIcons }) => {

    const notepadRef = useRef()
    const { name, iconImg } = desktopIcons;
    const { closeNotepadWindow, setIconFocused, setShowNotepadWindow } = useGlobalContext();
    const [notepadWindowSize, setNotepadWindowSize] = useState({ w: 400, h: window.innerHeight / 2 })

    //keep track of the size of the window
    const onResize = (event, { element, size, handle }) => {
        setNotepadWindowSize({ w: size.width, h: size.height })
    }



    return (
        <Draggable
            handle=".handle"
            bounds="parent"
        >
            <div className="window notepad-window" id="notepad" ref={notepadRef} onClick={() => setIconFocused('notepad')}>
                <ResizableBox
                    onResize={onResize}
                    resizeHandles={['sw']}
                    width={notepadWindowSize.w} height={notepadWindowSize.h}
                    minConstraints={[380, 250]} maxConstraints={[window.innerWidth / 2, window.innerHeight / 1.5]}
                >
                    <div className="handle">
                        <div className="window-info">
                            <img src={iconImg} alt={name} />
                            {name}
                        </div>
                        <div className="window-controls">
                            <button className="control" onClick={() => setShowNotepadWindow(false)}>
                                <img src={minIcon} alt="." />
                            </button>
                            <button className="control" >
                                <img src={maxIcon} alt="." />
                            </button>
                            <button className="control" onClick={() => closeNotepadWindow()}>
                                <img src={closeIcon} alt="." />
                            </button>
                        </div>
                    </div>
                    <div className="window-container">
                        <div className="window-inner-container">
                            <textarea></textarea>
                        </div>
                    </div>

                </ResizableBox>
            </div>
        </Draggable >




    )

}

export default Notepad;