import React, { useRef, useState } from 'react';
import '../../windows/window.scss'
import './about.scss';
import Draggable from 'react-draggable';
import { ResizableBox } from 'react-resizable';
import closeIcon from '../../../../assets/icons/close-icon.gif';
import minIcon from '../../../../assets/icons/mini-icon.gif';
import maxIcon from '../../../../assets/icons/max-icon.gif';
import portrait from '../../../../assets/0.jpeg';

import { useGlobalContext } from '../../../context';

const About = ({ desktopIcons }) => {

    const aboutRef = useRef()
    const { name, iconImg } = desktopIcons;
    const { closeAboutWindow, setIconFocused, setShowAboutWindow } = useGlobalContext();
    const [aboutWindowSize, setAboutWindowSize] = useState({ w: 400, h: "" })
    // const [aboutWindowPosition, setAboutWindowPosition] = useState({ top: window.innerHeight / 4, left: window.innerWidth / 4 })

    //keep track of the size of the window
    const onResize = (event, { element, size, handle }) => {
        setAboutWindowSize({ w: size.width, h: size.height })
    }


    return (
        <Draggable
            handle=".handle"
            bounds="parent">
            <div className="window about-window" id="about" ref={aboutRef} onClick={() => setIconFocused('about')}>
                <ResizableBox
                    onResize={onResize}
                    resizeHandles={[]}
                    width={aboutWindowSize.w} height={aboutWindowSize.h}
                    minConstraints={[380, 250]} maxConstraints={[window.innerWidth / 2.5, window.innerHeight / 1.5]}
                >
                    <div className="handle">
                        <div className="window-info">
                            <img src={iconImg} alt={name} />
                            {name}
                        </div>
                        <div className="window-controls">
                            <button className="control" onClick={() => setShowAboutWindow(false)}>
                                <img src={minIcon} alt="." />
                            </button>
                            <button className="control" >
                                <img src={maxIcon} alt="." />
                            </button>
                            <button className="control" onClick={() => closeAboutWindow()}>
                                <img src={closeIcon} alt="." />
                            </button>
                        </div>
                    </div>
                    <div className="window-container">
                        <div className="window-inner-container">
                            <p>I've been searching for a react project to do for the longest time. The majority of articles suggest the same thing. To do list, Calculator, background changer, use an API etc... I thought to myself why not include all of these in one project. Instead of following the design trends I thought I'll go back in time and challenge my CSS skills.</p>
                            <p>This was heavily inspired by <a href="https://www.bigdeskenergy.com">Big Desk Energy</a>.</p>

                            <div className="border-line"></div>
                            <div className="author-info">
                                <img src={portrait} alt="me" />
                                <div className="name">
                                    <a href="https://www.instagram.com/benfiika" target="_blank" rel="noopener noreferrer">Aimen Ben Aissa</a>
                                </div>
                            </div>
                        </div>
                    </div>

                </ResizableBox>
            </div>
        </Draggable >




    )

}

export default About;