import React, { useState } from 'react';
import './documents.scss'
import Draggable from 'react-draggable';
import { ResizableBox } from 'react-resizable';
import { useGlobalContext } from '../../../context'
import closeIcon from '../../../../assets/icons/close-icon.gif';
import minIcon from '../../../../assets/icons/mini-icon.gif';
import maxIcon from '../../../../assets/icons/max-icon.gif';
import instagramIcon from '../../../../assets/icons/instagram-icon.png';
import Icon from './icon';


const Documents = ({ desktopIcons }) => {

    const { name, iconImg } = desktopIcons;
    const { closeDocumentsWindow, setIconFocused, setShowDocumentsWindow } = useGlobalContext();
    const [documentsWindowSize, setDocumentstWindowSize] = useState({ w: 350, h: 200 })

    //keep track of the size of the window
    const onResize = (event, { element, size, handle }) => {
        setDocumentstWindowSize({ w: size.width, h: size.height })
    }

    return (
        <Draggable
            handle=".handle"
            bounds="parent"
        >

            <div className="window documents-window" id="document" onClick={() => setIconFocused('documents')}>
                <ResizableBox
                    onResize={onResize}
                    resizeHandles={['sw']}
                    width={documentsWindowSize.w} height={documentsWindowSize.h}
                    minConstraints={[300, 200]} maxConstraints={[400, 300]}
                >
                    <div className="handle">
                        <div className="window-info">
                            <img src={iconImg} alt={name} />
                            {name}
                        </div>
                        <div className="window-controls">
                            <button className="control" onClick={() => setShowDocumentsWindow(false)} >
                                <img src={minIcon} alt="." />
                            </button>
                            <button className="control">
                                <img src={maxIcon} alt="." />
                            </button>
                            <button className="control" onClick={() => closeDocumentsWindow()}>
                                <img src={closeIcon} alt="." />
                            </button>
                        </div>
                    </div>
                    <div className="window-container">
                        <div className="window-inner-container">
                            <Icon name="Instagram" link="https://www.instagram.com/benfiika/?hl=en" avatar={instagramIcon} />
                        </div>
                    </div>
                </ResizableBox>
            </div>
        </Draggable>
    )


}

export default Documents;